.header * {
  margin: 0;
}

.header {
  padding: 25px;
}

.header > * {
  display: inline-block;
}

.header li {
  display: inline-block;
  margin: 0 5px;
}

.header a {
  text-decoration: none;
  color: black;
}

.header a:hover {
  color: powderblue;
}



